import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import ReactMarkdown from "react-markdown"; //  
import remarkGfm from "remark-gfm"; //  
import { blogMetadata } from "../data/Blogs"; // 
import rehypeRaw from "rehype-raw";

export const ArticleItem = () => {
  const { blogId } = useParams();
  const [postContent, setPostContent] = useState(""); // Markdown
  const [loading, setLoading] = useState(true);

  // 1. URL
  const metadata = blogMetadata.find((p) => p.id === blogId);

  useEffect(() => {
    if (!metadata) {
      setLoading(false);
      return; // 
    }

    setLoading(true);

    // 2. Markdown
    // CRApublicsrcfetch
    // (srcimportfetch)
    fetch(metadata.markdownPath)
      .then((res) => {
        if (!res.ok) throw new Error("Markdown");
        return res.text(); // JSON
      })
      .then((markdownText) => {
        setPostContent(markdownText);
        setLoading(false);
      })
      .catch((err) => {
        console.error(":", err);
        setLoading(false);
      });
  }, [blogId, metadata]); // postId

  if (loading) {
    return <div>Markdown...</div>;
  }

  if (!metadata) {
    return <div>ID: {blogId}</div>;
  }

  return (
    <div
      style={{
        width: "90vw",
        maxWidth: "700px",
        marginTop: "50px",
        boxSizing: "border-box",
      }}
    >
      <img
        style={{
          width: "90vw",
          maxWidth: "700px",
          aspectRatio: "3/1",
          objectFit: "cover",
        }}
        src={`${process.env.PUBLIC_URL}/article-photo/${metadata.id}.jpg`}
        alt={`/article-photo/${metadata.id}.jpg`}
      />
      <h1>{metadata.title}</h1>
      <p>: {metadata.date}</p>

      {/* 3. ReactMarkdownHTML */}
      <ReactMarkdown remarkPlugins={[remarkGfm]} rehypePlugins={[rehypeRaw]}>
        {postContent}
      </ReactMarkdown>

      <Link
        to="/articles"
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        
      </Link>
    </div>
  );
};
